.about-user {
    width: 100%;
    max-width: 760px;
    margin-bottom: 28px;
    background: #ffffff;
    box-shadow: 1px 1px 3px 2px #dfdede;
}

.user-info-area {
    padding: 37px 40px 10px;
    border-bottom: 1px solid #eeeeee;
    font-size: 30px;
    line-height: 35px;
}

.user-info-area p {
    margin: 0;
    word-wrap: break-word;
}

.user-info-area p:not(:last-child) {
    margin-bottom: 20px;
}

.user-info-area span { color: #9090ff; }

.activity-area {
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 13px;
    list-style: none;
}

.activity-area__item {
    text-align: center;
    flex: 0 1 197px;
}

.activity-area__item span {
    vertical-align: center;
    font-size: 20px;
}

.activity-area__item a {
    display: flex;
    flex-direction: column;
    color: #000000;
    text-decoration: none;
}

@media screen and (max-width: 960px) {
    .about-user { max-width: unset; }
}

@media screen and (max-width: 540px) {
    .user-info-area {
        padding: 37px 10px 10px;
    }

    .user-info-area {
        font-size: 20px;
        line-height: 24px;
    }

    .activity-area { flex-wrap: wrap; }
}
