.sidebar {
    margin-right: 20px;
    padding: 28px 38px;
    background: #ffffff;
    box-shadow: 1px 1px 3px 2px #dfdede;
}

.sidebar__photo {
    display: block;
    width: 290px;
    height: 290px;
    object-fit: cover;
}

.user-photo-form {margin-bottom: 35px;}

.user-photo-form__label {
    display: block;
    position: relative;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;

    width: 290px;
    height: 290px;
    margin-top: -290px;
}

.user-photo-form__label:hover::before {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: rgba(96, 96, 96, 0.5);
}

.user-photo-form__label:hover::after {
    content: '';
    width: 30px;
    height: 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: url(ff76968022631653950465494d8935a0.svg) no-repeat center;
}

.sidebar-menu {
    margin: 0;
    padding: 0;
    list-style: none;
}

.sidebar-menu__item a {
    color: #9090ff;
    font-size: 22px;
    text-decoration: none;
    line-height: 35px;
}

@media screen and (max-width: 960px) {
    .sidebar {
        margin-right: 0;
        flex-basis: 100%;
        display: flex;
        margin-bottom: 30px;
    }

    .user-photo-form__label { margin-top: 0; }

    .user-photo-form { margin-left: -290px; }

    .sidebar-menu { margin-left: 30px; }
}

@media screen and (max-width: 540px) {
    .sidebar {
        flex-wrap: wrap;
        justify-content: center;
        padding: 10px;
    }

    .sidebar-menu {
        margin-left: 0;
        flex-basis: 100%;
        text-align: center;
    }
}
