body {
    margin: 0;
    background: #f1f1f1;
    font-family: Open Sans, Arial, sans-serif;
}

.main {
    max-width: 1170px;
    margin: 0 auto;
    padding: 15px 10px 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.visually-hidden {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
}

@media screen and (max-width: 960px) {
    .main { flex-wrap: wrap; }

    .info-area { flex-basis: 100%; }
}
