.friends-list-wrapper {
    padding: 0 42px 11px;
}

.friends-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.friends-list__item {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 115px;
    padding: 16px 0;
    align-content: flex-start;
}

.friends-list__item img {
    flex-basis: 84%;
    width: 115px;
    margin-right: 33px;
    border-radius: 50%;
}

.friends-list__item p {
    margin: 5px 0;
    font-size: 20px;
    line-height: 26px;
}

.friends-list__item span {
    color: #9090ff;
}

@media screen and (max-width: 540px) {
    .friends-list-wrapper { padding: 11px;}

    .friends-list__item {
        height: auto;
    }
}
