.feature-wrapper {
    width: 100%;
    max-width: 760px;
    margin-bottom: 45px;
    background: #fff;
    box-shadow: 1px 1px 3px 2px #dfdede;
}

.features-tabs {
    display: flex;
    border-bottom: 1px solid #eeeeee;
}

.features-tabs__item {
    flex: 0 0 33%;
    margin-left: 10px;
    padding: 25px 0 14px;
    border: none;
    font-size: 16px;
    line-height: 20px;
    background: transparent;
    box-shadow: none;
}

.features-tabs__item--active {
    border-bottom: 2px solid #eeeeee;
}

@media screen and (max-width: 960px) {
    .feature-wrapper { max-width: unset; }
}
